* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  color: rgb(216, 216, 216);
  font-family: 'Signika', sans-serif;
}

html {
  font-size: calc(12px + (24 - 12) * ((100vw - 200px) / (1920 - 200)));
}

@media(max-width: 960px){
  html {
    font-size: calc(18px + (24 - 18) * ((100vw - 200px) / (960 - 200)));
  }
}


@font-face {
  font-family: 'Leixo';
  src: local('Leixo'), url(/static/media/LEIXO-DEMO.7e5b0ae7.ttf) format('truetype');
}

*{
  font-family: Leixo;
  font-size: 1.5rem;
  color: rgb(192, 192, 192);
}

.App{
  background-color: rgb(0, 0, 0);
  background-image: url(/static/media/bkA.b30871cc.png);
  background-repeat: no-repeat;
  overflow: hidden;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

p{
  line-height: 1.3rem;
}

.aboutMeBack{
  background-image: url(/static/media/PagWebBio.3d61f475.jpg);
  background-size: 100vw;
}

.main{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  height: 100vh;
  width: 100%;
  background-size: 100%;
}

.opcion--section{
  display: -webkit-flex;
  display: flex;
  height: 50vh;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  z-index: 3;
}

.unboton{
  z-index: 10;
}

.heightAllSections{
  margin-top: 2vh;
  min-height: 70vh;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #331b45;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(152, 152, 152,0.3);
  -webkit-transition: 1.9s all;
  transition: 1.9s all;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(230, 176, 28, 0.7);
}

@media(max-width: 960px){
  .App{
    overflow: auto;
  }
  .main{
    margin-top: 14vh;
    min-height: 72vh;
    overflow: auto;
    overflow-x: hidden;
  }
  .aboutMeBack{
    background-size: auto 100vh;
    background-position: 89%;
  }
}
